import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";
import { MenuPage } from "./menu.page";

const routes: Routes = [
  {
    path: "",
    component: MenuPage,
    children: [
      {
        path: "",
        redirectTo: "home",
        pathMatch: "full"
      },
      {
        path: "home",
        loadChildren: () =>
          import("../home/home.module").then(m => m.HomePageModule)
      },
      { path: 'login', loadChildren: () => import('../login/login.module').then( m => m.LoginPageModule)},
      {
        path: "menu/home",
        redirectTo: "home",
        pathMatch: "full"
      },
      {
        path: 'obras',
        loadChildren: () => import('./../pages/obra/obras/obras.module').then( m => m.ObrasPageModule)
      },
      {
        path: 'obra-inserir',
        loadChildren: () => import('./../pages/obra/obra-inserir/obra-inserir.module').then( m => m.ObraInserirPageModule)
      },
      {
        path: 'empresas',
        loadChildren: () => import('./../pages/empresa/empresas/empresas.module').then( m => m.EmpresasPageModule)
      },
      {
        path: 'empresa-inserir',
        loadChildren: () => import('./../pages/empresa/empresa-inserir/empresa-inserir.module').then( m => m.EmpresaInserirPageModule)
      },
      {
        path: 'usuarios',
        loadChildren: () => import('./../pages/usuario/usuarios/usuarios.module').then( m => m.UsuariosPageModule)
      },
      {
        path: 'usuario-inserir',
        loadChildren: () => import('./../pages/usuario/usuario-inserir/usuario-inserir.module').then( m => m.UsuarioInserirPageModule)
      },
      {
        path: 'tipoinspecoes',
        loadChildren: () => import('./../pages/tipoinspecao/tipoinspecoes/tipoinspecoes.module').then( m => m.TipoinspecoesPageModule)
      },
      {
        path: 'tipoinspecao-inserir',
        loadChildren: () => import('./../pages/tipoinspecao/tipoinspecao-inserir/tipoinspecao-inserir.module').then( m => m.TipoinspecaoInserirPageModule)
      },
      {
        path: 'itenstipoinspecao',
        loadChildren: () => import('./../pages/tipoinspecao/itenstipoinspecao/itenstipoinspecao.module').then( m => m.ItenstipoinspecaoPageModule)
      },
      {
        path: 'statusitensinspecoes',
        loadChildren: () => import('./../pages/statusitensinspecao/statusitensinspecoes/statusitensinspecoes.module').then( m => m.StatusitensinspecoesPageModule)
      },
      {
        path: 'statusitensinspecao-inserir',
        loadChildren: () => import('./../pages/statusitensinspecao/statusitensinspecao-inserir/statusitensinspecao-inserir.module').then( m => m.StatusitensinspecaoInserirPageModule)
      },
      {
        path: 'inspecoes',
        loadChildren: () => import('./../pages/inspecao/inspecoes/inspecoes.module').then( m => m.InspecoesPageModule)
      },
      {
        path: 'inspecao-inserir',
        loadChildren: () => import('./../pages/inspecao/inspecao-inserir/inspecao-inserir.module').then( m => m.InspecaoInserirPageModule)
      },
      {
        path: 'inspecao-realizar',
        loadChildren: () => import('./../pages/inspecao/inspecao-realizar/inspecao-realizar.module').then( m => m.InspecaoRealizarPageModule)
      },
      {
        path: 'tarefas',
        loadChildren: () => import('./../pages/dashboard/tarefas/tarefas.module').then( m => m.TarefasPageModule)
      },
      {
        path: 'perfil-usuario',
        loadChildren: () => import('./../pages/permissoes/perfil-usuario/perfil-usuario.module').then( m => m.PerfilUsuarioPageModule)
      },
      {
        path: 'perfil-usuario-inserir',
        loadChildren: () => import('./../pages/permissoes/perfil-usuario-inserir/perfil-usuario-inserir.module').then( m => m.PerfilUsuarioInserirPageModule)
      },
      {
        path: 'permissao-usuario',
        loadChildren: () => import('./../pages/permissoes/permissao-usuario/permissao-usuario.module').then( m => m.PermissaoUsuarioPageModule)
      },
      {
        path: 'selecao-relatorio',
        loadChildren: () => import('./../pages/relatorios/selecao-relatorio/selecao-relatorio.module').then( m => m.SelecaoRelatorioPageModule)
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class MenuPageRoutingModule {}
